.homeCenter
{
  background-color: rgba(255, 0, 0, 0.6);
  margin: 125px auto;
  width: 250px;
  height: 250px;
  -webkit-animation-name: myanimation;
          animation-name: myanimation;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes myanimation {
  0% {color: rgb(255, 255, 255); background-color: rgb(255, 0, 0) ; border-radius: 0 0 0 0; border: 3px solid rgb(255, 255, 255);}
  25% {color: rgb(255, 0, 200); background-color: rgb(0, 0, 0);  border-radius: 50% 0 0 0; border: 3px solid rgb(255, 0, 200);}
  50% {color: rgb(0, 0, 0); background-color: yellow; border-radius: 50% 50% 0 0; border: 3px solid rgb(0, 0, 0);}
  75% {color: red; background-color: rgb(255, 255, 255); border-radius: 50% 50% 50% 0; border: 3px solid red;}
  100% {color: rgb(255, 255, 255); background-color: rgb(0, 30, 60); border-radius: 50% 50% 50% 50%; border: 3px solid rgb(255, 0, 0);}
}

@keyframes myanimation {
  0% {color: rgb(255, 255, 255); background-color: rgb(255, 0, 0) ; border-radius: 0 0 0 0; border: 3px solid rgb(255, 255, 255);}
  25% {color: rgb(255, 0, 200); background-color: rgb(0, 0, 0);  border-radius: 50% 0 0 0; border: 3px solid rgb(255, 0, 200);}
  50% {color: rgb(0, 0, 0); background-color: yellow; border-radius: 50% 50% 0 0; border: 3px solid rgb(0, 0, 0);}
  75% {color: red; background-color: rgb(255, 255, 255); border-radius: 50% 50% 50% 0; border: 3px solid red;}
  100% {color: rgb(255, 255, 255); background-color: rgb(0, 30, 60); border-radius: 50% 50% 50% 50%; border: 3px solid rgb(255, 0, 0);}
}

